.vacancy {
  background-color: #1c1c1c;
  padding-bottom: 20px;
  // .vacancy__title

  &__title {
    margin: 0;
    margin-bottom: 70px;
    @include small-tablet{
      font-size: 45px;
      margin-bottom: 50px;
      line-height: 100%;
    }
  }

  // .vacancy__content

  &__content {
    display: flex;
    flex-direction: column;
    gap: 60px;
    @include small-tablet{
      gap: 25px;
    }
  }

  // .vacancy__item

  &__item {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 460px);
    @include tablet{
      grid-template-columns: 1fr 1fr;
    }
    @include small-tablet{
      grid-template-columns: 1fr;
      padding-bottom: 27px;
      border-radius: 0 0 10px 10px;
      border-bottom: 1px solid #8d8d8d;
      gap: 0;
    }
  }

  // .vacancy__item-info

  &__item-info {
    display: flex;
    align-items: flex-start;
    gap: clamp(1.563rem, -0.142rem + 8.52vw, 6.25rem);
    padding-bottom: 24px;
    border-radius: 0 0 10px 10px;
    border-bottom: 1px solid #8d8d8d;
    padding-right: 30px;
    @include small-tablet{
      border: 0;
      padding: 0;
    }
  }

  // .vacancy__item-num

  &__item-num {
    font-weight: 400;
    font-size: 26px;
    line-height: 100%;
    color: #fff;
    opacity: 0.4;
    @include small-tablet{
      display: none;
    }
  }

  // .vacancy__item-inner

  &__item-inner {
    max-width: 320px;
    @include small-tablet{
      max-width: 100%;
    }
  }

  // .vacancy__item-name

  &__item-name {
    font-weight: 700;
    font-size: 26px;
    line-height: 100%;
    color: #fff;
    margin-bottom: 18px;
    @include mobile{
      font-size: 20px;
      margin-bottom: 10px;
    }
  }

  // .vacancy__item-text

  &__item-text {
    color: #ededed;
    margin: 0;
    margin-bottom: 18px;
    @include mobile{
      margin-bottom: 20px;
    }
  }

  // .vacancy__item-link

  &__item-link {
    padding: 25px 15px;
    @include small-tablet{
      width: 100%;
    }
  }

  // .vacancy__item-condition

  &__item-condition {
    padding-bottom: 24px;
    border-radius: 0 0 10px 10px;
    border-bottom: 1px solid #8d8d8d;

    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 20px;
    @include small-tablet{
      border: 0;
      flex-direction: column;
      padding: 0;
    }
  }

  // .vacancy__item-condition-info

  &__item-condition-info {
    @include small-tablet{
      display: flex;
      flex-wrap: wrap;
      border-radius: 15px;
      background-color: white;
      padding: 17px;
      width: 100%;
    }
  }

  // .vacancy__item-condition-name

  &__item-condition-name {
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    color: #fff;
    opacity: 0.4;
    margin-bottom: 30px;
    @include small-tablet{
      color: #1f221e;
      width: 100%;
      margin-bottom: 12px;
    }
  }

  // .vacancy__item-condition-text

  &__item-condition-text {
    font-weight: 700;
    font-size: 26px;
    line-height: 100%;
    color: #fff;
    margin-bottom: 20px;
    @include small-tablet{
      color: black;
      width: 50%;
      margin: 0;
    }
    @include mobile{
      font-size: 20px;
    }
  }

  // .vacancy__item-condition-img

  &__item-condition-img {
    max-width: 160px;
    @include small-tablet{
      display: none;
    }
  }
}
