.cosmetic {
  padding-bottom: 20px;
  background-color: #1c1c1c;
  // .cosmetic__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 470px) minmax(200px, 530px);
    justify-content: space-between;
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
      justify-items: center;
    }
  }

  // .cosmetic__info

  &__info {
  }

  // .cosmetic__title

  &__title {
    line-height: 100%;
    margin: 0;
    margin-bottom: 30px;
  }

  // .cosmetic__text

  &__text {
    line-height: 130%;
    color: #ededed;
    margin: 0;
  }

  // .cosmetic__brands

  &__brands {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px 20px;
    @include mobile{
      grid-template-columns: repeat(2, 1fr);
      gap: 10px;
    }
  }

  &__brand-wrapper{

  }

  // .cosmetic__brand

  &__brand {
    background-color: white;
    border-radius: 20px;
    padding: 25px 17px;
    width: 100%;
    grid-column: 2 span;
    @include mobile{
      grid-column: unset;
      padding: 17px;
    }
    &:nth-child(1) {
      grid-column: 2/4;
      @include mobile{
        grid-column: unset;
      }
      .cosmetic__brand-logo {
        img {
          max-width: 105px;
          @include mobile{
            max-width: 100px;
          }
        }
      }
    }
    &:nth-child(2) {
      grid-column: 4/6;
      @include mobile{
        grid-column: unset;
      }
      .cosmetic__brand-logo {
        img {
          max-width: 105px;
          @include mobile{
            max-width: 100px;
          }
        }
      }
    }
    &:nth-child(3) {
      @include mobile{
        order: 5;
        grid-column: 2 span;
      }
      .cosmetic__brand-logo {
        img {
          max-width: 71px;
          @include mobile{
            max-width: 70px;
          }
        }
      }
    }
    &:nth-child(4) {
      .cosmetic__brand-logo {
        img {
          max-width: 105px;
          margin-left: 10px;
          @include mobile{
            max-width: 100px;
          }
        }
      }
    }
    &:nth-child(5) {
      .cosmetic__brand-logo {
        img {
          max-width: 105px;
          @include mobile{
            max-width: 100px;
          }
        }
      }
    }
  }

  // .cosmetic__brand-logo

  &__brand-logo {
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 15px;
    background-color: #1c1c1c;
    border-radius: 100%;
    img {
      width: 100%;
    }
  }

  // .cosmetic__brand-name

  &__brand-name {
    font-weight: 700;
    @include fluid-text(15, 12);
    line-height: 130%;
    text-align: center;
    color: #313131;
  }
}
