.useful {
  background-color: #1c1c1c;
  padding-bottom: 20px;
  // .useful__title

  &__title {
    line-height: 100%;
    margin-bottom: 35px;
  }

  // .useful__content

  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 23px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .useful__link

  &__link {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: white;
    @include hover{
      .useful__link-title{
        color: var(--accent-color);
      }
      .useful__link-more{
        color: var(--accent-color);
      }
    }
  }

  // .useful__link-img

  &__link-img {
    margin-bottom: 25px;
  }

  // .useful__link-title

  &__link-title {
    font-weight: 700;
    @include fluid-text(25, 20);
    line-height: 87%;
    letter-spacing: -0.04em;
    color: #fff;
    margin-bottom: 10px;
    transition: all .2s ease-in;
  }

  // .useful__link-text

  &__link-text {
    color: rgba(255, 255, 255, 0.85);
    line-height: 140%;
    margin: 0;
    margin-bottom: 14px;
  }

  // .useful__link-more

  &__link-more {
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    color: #fff;
    transition: all .2s ease-in;
  }
}
