.hero-discounts {
  .hero__info{
    max-width: 420px;
    @include tablet{
      max-width: unset;
    }
  }
  .hero__promocode-link{
    width: 100%;
    max-width: 350px;
    @include mobile{
      margin-bottom: 80px;
    }
  }
  // .hero-discounts__condition

  &__condition {
    border-radius: 20px;
    background-color: white;
    display: flex;
    align-items: center;
    gap: 15px;
    max-width: 350px;
    font-weight: 700;
    font-size: 15px;
    line-height: 110%;
    color: #202020;
    padding: 8px;
    margin-bottom: 20px;
  }

  // .hero-discounts__icon

  &__icon {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(200, 164, 106, 0.4);
    border: 1px solid #c8a46a;
    flex: none;
    border-radius: 16px;
  }

  // .hero-discounts__text

  &__text {
    font-weight: 300;
    font-size: 15px;
    line-height: 110%;
    color: #ededed;
    opacity: 0.5;
    margin-bottom: 24px;
    span{
      font-weight: 700;
      display: inline;
    }
  }
}
