html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;

}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: var(--black-color);
  font-weight: 300;
  line-height: 110%;
  background-color: white;

  @include fluid-text(16, 14)
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-right {
  padding-left: max(15px, (100% - 1170px) / 2);
  padding-right: 15px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-mobile{
  @include mobile{
    display: none !important;
  }
}

.hidden-tablet{
  @include tablet{
    display: none !important;
  }
}

.hidden-small-tablet{
  @include small-tablet{
    display: none !important;
  }
}

.visible-tablet{
  @include for-tablet{
    display: none !important;
  }
}

.visible-small-tablet{
  @include for-small-tablet{
    display: none !important;
  }
}

.visible-mobile{
  @include for-mobile{
    display: none !important;
  }
}

h1, .h1{
  @include fluid-text(72, 29);
  line-height: 150%;
  font-weight: 900;
  font-style: italic;
  font-family: var(--font-family-accent);
  text-transform: uppercase;
  letter-spacing: 0.03em;

  background: #CBCACC;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(23%, #CBCACC), to(#FFFFFF));
  background: -o-linear-gradient(top, #CBCACC 23%, #FFFFFF 100%);
  background: linear-gradient(to bottom, #CBCACC 23%, #FFFFFF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h2, .h2{
  @include fluid-text(65, 40);
  line-height: 100%;
  font-weight: 900;
  font-family: var(--font-family-accent);
  font-style: italic;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

h3, .h3{
  @include fluid-text(40, 30);
  line-height: 100%;
  font-weight: 700;
  font-family: var(--font-family-accent);
  font-style: italic;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

h4, .h4{
  @include fluid-text(26, 16);
  line-height: 100%;
  font-weight: 700;
}

h5, .h5{
  @include fluid-text(22, 16);
  line-height: 100%;
  font-weight: 700;
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition: all var(--transition-duration) ease-in;
}

span{
  display: block;
}

input, select{
  outline: transparent;
}

.list{
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
  &__item{
    padding-left: 22px;
    position: relative;
    font-weight: 600;

    &::after{
      display: block;
      content: "";
      width: 8px;
      height: 8px;
      border-radius: 100%;
      background-color: var(--accent-color);
      position: absolute;
      top: 5px;
      left: 0;
    }
  }
}

.tabs{
  @include tabs
}

.btn-1{
  background-color: var(--accent-color);
  color: var(--light-color);
  padding: 15px 25px;
  font-weight: 700;
  transition: all .2s ease-in;
  display: inline-block;
  border-radius: 20px;
  text-align: center;
  @include hover{
    background-color: #b79154;
  }
  @include mobile{
    padding: 25px;
  }
}

.btn-2{
  background-color: var(--black-color);
  color: var(--light-color);
  padding: 15px 25px;
  border-radius: 50px;
  font-weight: 700;
  transition: all .2s ease-in;
  display: inline-block;
  @include hover{
    background-color: var(--accent-color);
    color: var(--light-color);
  }
}

.ymaps-layers-pane {
	filter: grayscale(1);
	-ms-filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}

.custom-balloon{
  text-align: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 117%;
  text-align: center;
  color: #fff;
  background-color: var(--accent-color);
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: 100%;
  width: 100%;
  padding: 15px;
  border-radius: 10px;
    strong{
      font-weight: 500;
      font-size: 14px;
      line-height: 132%;
      text-align: center;
      color: #fff;
    }
}

.ymaps-map{
  width: 100% !important;
}

.ymaps-b-balloon__content{
  background-color: transparent !important;
}

.ymaps-b-balloon__content-holster{
  padding: 0 !important;
}

.ymaps-b-balloon__tr{
  display: none !important;
}

.ymaps-b-balloon__tl{
  display: none !important;
}

.ymaps-b-balloon__bl{
  display: none !important;
}

.ymaps-b-balloon__br{
  display: none !important;
}

.ymaps-shadows-pane{
  display: none !important;
}

.ya-map-style{
  @include mobile{
    height: 400px !important;
  }
}

.breadcrumb {
  display: flex;
  gap: 8px;
  padding-top: 25px;
  margin-bottom: 50px;
  @include small-tablet{
    margin-bottom: 20px;
    padding-top: 35px;
  }
  // .breadcrumb__item

  &__item {
    display: flex;
    gap: 8px;
    align-items: center;
    &:not(:first-child){
      &::before{
        display: block;
        content: "/";
        color: white;
        font-weight: 500;
        font-size: 14px;
      }
    }
  }

  // .breadcrumb__link

  &__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 157%;
    color: #545454;
    @include mobile{
      font-weight: 400;
    }
    &--active{
      color: #7b8990;
    }
  }
}

.swiper-button-disabled.swiper-button-lock{
  display: none;
}

.accent-color{
  color: var(--accent-color);
}

