.hero-about{

  &__text{
    max-width: 367px;
  }

}

.about-advantages {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .about-advantages__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border: 1px solid #c3c3c3;
    border-radius: 20px;
    padding: 20px 50px;
    @include tablet{
      padding: 20px;
    }
    @include small-tablet{
      border: 0;
      grid-template-columns: repeat(2, 1fr);
      padding: 0;
      gap: 25px;
    }
    @include mobile{
      grid-template-columns: 1fr;

    }
  }

  // .about-advantages__item

  &__item {
    min-height: 310px;
    border-bottom: 1px solid #d9d9d9;
    margin: 0 10px;
    position: relative;
    @include small-tablet{
      padding: 0 !important;
      border: 0 !important;
      min-height: unset;
    }
    @include mobile{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &:not(:nth-child(1), :nth-child(4)){
      padding: 35px 50px;
      @include tablet{
        padding: 25px 30px;
      }
    }
    &:nth-child(1), &:nth-child(4){
      padding: 35px 50px 35px 0;
    }
    &:nth-child(4){
      border: 0;
    }
    &:nth-child(5){
      border: 0;
    }
    &:nth-child(6){
      border: 0;
    }
    &:nth-child(2), &:nth-child(5){
      &::after{
        display: block;
        content: "";
        position: absolute;
        top: 0;
        left: -10px;
        bottom: 10px;
        width: 1px;
        background-color: #d9d9d9;
        @include small-tablet{
          display: none;
        }
      }

      &::before{
        display: block;
        content: "";
        position: absolute;
        top: 0;
        right: -10px;
        bottom: 10px;
        width: 1px;
        background-color: #d9d9d9;
        @include small-tablet{
          display: none;
        }
      }
    }
    &:nth-child(5){
      &::after{
       top: 10px;
      }

      &::before{
        top: 10px;
       }
    }
  }

  // .about-advantages__item-icon

  &__item-icon {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--accent-color);
    border-radius: 10px;
    margin-bottom: 25px;
    @include mobile{
      margin-bottom: 15px;
    }
  }

  // .about-advantages__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 12px;
    @include mobile{
      margin-bottom: 5px;
      br{
        display: none;
      }
    }
  }

  // .about-advantages__item-text

  &__item-text {
    margin: 0;
    color: #1c1c1c;
    font-weight: 300;
  }
}

.about-photos {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .about-photos__head

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 65px;
    @include mobile{
      margin-bottom: 25px;
    }
  }

  // .about-photos__title

  &__title {
    margin: 0;
  }

  // .about-photos__head-text

  &__head-text {
    max-width: 100px;
    text-align: right;
    color: rgba(28, 28, 28, 0.6);
    @include small-tablet{
      display: none;
    }
  }

  // .about-photos__swiper-wrapper

  &__swiper-wrapper {
    display: flex;
    align-items: flex-end;
    gap: clamp(1.563rem, -0.142rem + 8.52vw, 6.25rem);
    @include small-tablet{
      flex-direction: column-reverse;
      align-items: unset;
    }
  }

  &.photos{
    .about-photos__swiper-content{
      margin-right: -15px;
    }
  }

  &__swiper-content{
    min-width: 100px;
    @include mobile{
      margin-right: -15px;
    }
  }

  // .about-photos__swiper-navigation-btns

  &__swiper-navigation-btns {
    display: flex;
    gap: 5px;
    @include small-tablet{
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }
  }

  // .about-photos__swiper-navigation-btn

  &__swiper-navigation-btn {
  }

  // .about-photos__swiper-navigation-prev

  &__swiper-navigation-prev {
  }

  // .about-photos__swiper-scroll

  &__swiper-scroll {
    display: none;
    @include small-tablet{
      width: 100%;
      display: block;
      height: 1px;
      margin: 0 20px;
      .swiper-scrollbar-drag{
        height: 4px;
        background-color: var(--accent-color);
        border-radius: 0;
        margin-top: -4px;
      }
    }
  }

  // .about-photos__swiper-navigation-next

  &__swiper-navigation-next {
  }

  // .about-photos__swiper

  &__swiper {
  }

  // .about-photos__swiper-img

  &__swiper-img {
    width: 100%;
    border-radius: 20px;
    @include mobile{
      border-radius: 10px;
    }
    &-300{
      max-height: 300px;
    }
  }
}
