.header{
  padding: 10px 0;
  position: relative;
  background-color: #2a2a2a;
  @include small-tablet{
    padding: 15px 0;
  }
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__logo{
    img{
      max-width: 50px;
    }

  }

		// .header__address

		&__address {
      display: flex;
      align-items: center;
      gap: 13px;
      @include small-tablet{
        display: none;
      }
		}

		// .header__address-city

		&__address-city {
      display: inline;
      color: #ededed;
		}

		// .header__address-place

		&__address-place {
      display: inline;
      font-weight: 700;
      color: #ededed;
		}

		// .header__phone

		&__phone {
      font-weight: 700;
      font-size: 18px;
      color: #ededed;
      @include small-tablet{
        font-size: 14px;
        margin-left: auto;
      }
      @include hover{
        color: var(--accent-color);
      }
		}

}

.logo {
  position: relative;
  z-index: 7;
}

.menu{
  @include tablet{
  position: fixed;
  background-color: white;
  bottom: 0;
  top: 0;
  right: -100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  align-items: center;
  transition: right .2s ease-in;
  z-index: 12;
  &--active{
    right: 0;
  }
  }
  &__item {

    &-logo{
      max-width: 72px;
    }

  }


  &__list {
    display: flex;
    align-items: center;
    column-gap: 25px;
    @include tablet{
      flex-direction: column;
      row-gap: 20px;
      text-align: center;
      margin: 0 auto
       }
  }
  &__link{
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: #ededed;
    position: relative;
    padding-bottom: 5px;
    @include tablet{
      color: black;
    }
    &::after{
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background: #fff;
      opacity: 0.1;
      transition: all .2s ease-in;
    }
    &--active{
      color: #fff;
      @include tablet{
        color: rgb(65, 64, 64);
      }
      &::after{
        opacity: 1;
      }
    }
    @include hover{
      color: var(--accent-color);
      &::after{
        opacity: 1;
      }

    }
  }

}

.burger{
  @include burger;
  display: none;
  @include tablet{
  display: block;
  z-index: 13;
  &--active{
    color: black;
  }
 }
}
