.hero {
  background-image: url(../img/hero-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 100px;
  position: relative;
  overflow: hidden;
  min-height: calc(100dvh - 96px);
  @include tablet{
    background-image: none;
    border-radius: 0;
  }
  @include small-tablet{
    margin-bottom: 50px;
  }


  &__uptitle{
    font-weight: 400;
    font-size: 12px;
    line-height: 118%;
    color: #fff;
    margin-bottom: 10px;
    @include tablet{
      display: none;
    }
  }

  &__subtitle{
    font-weight: 700;
    font-size: 16px;
    line-height: 110%;
    color: #ededed;
    margin-bottom: 35px;
    @include tablet{
      display: none;
    }
  }

  &__info-tags{
    display: flex;
    gap: 10px;
    margin-bottom: 35px;
    @include tablet{
      gap: 10px;
      margin-bottom: 20px;
    }
  }

  &__text-num{
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__num{
    font-family: var(--font-family-accent);
    font-style: italic;
    font-weight: 900;
    font-size: 72px;
    line-height: 100%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: white;
  }

  &__info-tag{
    color: white;
    padding: 13px;
    border-radius: 25px;
    display: flex;
    align-items: center;
    @include tablet{
      padding: 10px;
    }
    &--border{
      border: 1px solid white;
    }

    &--white{
      color: black;
      background-color: white;
    }
  }

  // .hero__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    @include big-desktop{
      grid-template-columns: 1fr 1fr;
    }
    @include tablet{
      gap: 20px;
    }
    @include tablet{
      grid-template-columns: 1fr;
      min-height: calc(100dvh - 58px);
      margin-bottom: 13px;
    }
  }

  // .hero__info

  &__info {
    padding: 70px 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @media (max-height: 800px) {
      padding: 40px 0;
    }
    @include tablet{
      z-index: 2;
      position: relative;
      padding: 30px 0;
      position: absolute;
      bottom: 0;
      width: 100%;
    }
  }

  &__info-content{
    @include tablet{
      padding: 0 15px;
      width: 100%;
    }
  }

  // .hero__info-img

  &__info-img {
    margin-bottom: 10px;
    max-width: 116px;
    @media (max-height: 800px){
      max-width: 80px;
    }
    @include tablet{
      margin: 0;
    }
    @include mobile{
      max-width: 52px;
    }
  }

  &__info-title-wrapper{
    @include tablet{
      display: flex;
      align-items: center;
      gap: 5px;
      margin-bottom: 12px;
    }
  }

  // .hero__title

  &__title {
    text-transform: uppercase;
    margin: 0;
    color: var(--light-color);
    margin-bottom: 40px;
    line-height: 100%;
    letter-spacing: 0.03em;
    padding-left: 5px;
    padding-right: 5px;
    background: #A2A2A2;
background: linear-gradient(to bottom, #A2A2A2 0%, #ECECEC 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
    // @media (max-height: 768px) {
    //   font-size: 40px;
    // }
    @media (max-height: 800px) {
      margin-bottom: 20px;
    }
    @include tablet{
      margin: 0;
    }
  }

  // .hero__promocode

  &__promocode {
    display: inline-flex;
    align-items: center;
    position: relative;
    width: 350px;
    margin-bottom: 20px;
    @include tablet{
      margin-bottom: 30px;
    }
    svg{
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 100%;
      z-index: 0;
      @include tablet{
        left: -30px;
        top: -6px;
       }
    }
  }

  // .hero__promocode-img

  &__promocode-img {
    position: relative;
    z-index: 2;
    @include tablet{
      width: 50px;
    }
  }

  // .hero__promocode-text

  &__promocode-text {
    font-weight: 700;
    color: #ededed;
    position: relative;
    z-index: 2;
  }

  // .hero__text

  &__text {
    color: #ededed;
    margin-bottom: 23px;
    font-weight: 300;
    max-width: 490px;
    a{
      font-weight: 700;
      font-size: 22px;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      color: white;
    }
  }

  // .hero__promocode-link

  &__promocode-link {
    background-color: var(--accent-color);
    padding: 25px 90px;
    border-radius: 20px;
    display: inline-block;
    margin-bottom: 37px;
    color: #fff;
    font-weight: 700;
    @media(max-height: 800px){
      margin-bottom: 20px;
    }
    @include tablet{
      width: 100%;
      margin: 0;
      align-self: end;
      padding: 25px;
      margin-bottom: 20px;
    }
  }

  // .hero__addition

  &__addition {
    color: #ededed;
    opacity: 0.4;
    font-weight: 300;
  }

  // .hero__video-wrapper

  &__video-wrapper {
    max-height: calc(100dvh - 96px);
    margin-right: -13px;
    @include tablet{
      position: absolute;
      top: 0;
      bottom: 0;
      max-height: unset;
      left: -15px;
      right: -15px;
      border-radius: 0 0 20px 20px;
      overflow: hidden;
      margin: 0;
    }
  }

  // .hero__video

  &__video {
    width: 100%;
    height: 100%;
    border-radius: 120px 0 120px 120px;
    object-fit: cover;
    min-height: calc(100dvh - 96px);
    @include tablet{
      border-radius: 0;
      min-height: unset;
    }
  }

  &__img-wrapper {
    max-height: calc(100dvh - 96px);
    margin-right: -15px;
    @include tablet{
      position: absolute;
      top: 0;
      bottom: 0;
      max-height: unset;
      left: -15px;
      right: -15px;
      border-radius: 0;
      overflow: hidden;
      margin: 0;
    }
  }

  // .hero__img

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    min-height: calc(100dvh - 96px);
    @include tablet{
      min-height: unset;
    }
    @include mobile{}
  }
}

.hero-tamplate{
  .hero__info{
    padding-top: 0;
  }
  .hero__img{
    object-position: right;
    @include tablet{
      height: unset !important;
    }
  }
  padding-top: 0;
  @include tablet{
    .hero__title{
      font-size: 45px;
    }
    .hero__content{
      margin: 0 -15px;
    }
    .hero__info{
      position: static;
      background-color: #1f221e;
      padding: 0;
      justify-content: space-between;
    }
    .hero__promocode-link{
      margin-bottom: 25px;
    }
    .hero__consultation-link{
      margin-bottom: 35px;
    }
    .hero__addition{
      margin-bottom: 40px;
    }
    .hero__img{
      border-radius: 0
    }
  }
}

.hero-training{
  .hero__title{
    margin-bottom: 20px;
  }
}

.advantage {
  margin-bottom: 100px;
  @include small-tablet{
    margin-bottom: 50px;
  }
  // .advantage__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 13px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .advantage__item

  &__item {
    display: flex;
    align-items: center;
    background-color: #232323;
    border-radius: 20px;
    color: white;
    padding: 55px 20px 38px 20px;
    position: relative;
    overflow: hidden;
    @include small-tablet{
      align-items: unset;
    }
    &:nth-child(1){
      padding-left: 180px;
      @include tablet{
        padding-left: 80px;
      }
      @include small-tablet{
        padding: 25px 20px;
      }
      .advantage__item-img{
        left: 50px;
        bottom: unset;
        top: 0;
        max-width: 82px;
        @include tablet{
          top: 40px;
          left: 15px;
          max-width: 36px;
        }
        @include small-tablet{
          inset: unset;
          right: 25px;
          top: 12px;

        }
      }
    }
    &:nth-child(2){
      padding-left: 270px;
      @include tablet{
        padding-left: 170px;
      }
      @include mobile{
        padding: 25px 20px;
        padding-bottom: 250px;
      }
      .advantage__item-img{
        max-width: 230px;
        @include tablet{
          max-width: 150px;
        }
        @include mobile{
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    &:nth-child(3){
      padding-left: 322px;
      @include tablet{
        padding-left: 250px;
      }
      @include mobile{
        padding: 25px 20px;
        padding-bottom: 170px;
      }
      .advantage__item-img{
        max-width: 330px;
        @include tablet{
          max-width: 250px;
        }
      }
    }
    &:nth-child(4){
      padding-left: 130px;
      @include tablet{
        padding-left: 70px;
      }
      @include mobile{
        padding: 25px 20px;
        padding-bottom: 200px;
      }
      .advantage__item-img{
        bottom: 25px;
        max-width: 141px;
        @include tablet{
          max-width: 80px;
        }
        @include mobile{
          max-width: 170px;
          bottom: 10px;
          left: 50%;
          transform: translateX(-50%);

        }
      }
    }
    &--white{
      color: black;
      background-color: #f6f6f6;
      .advantage__item-text{
        color: black;
      }
      .advantage__item-num{
        color: #1c1c1c;
      }
    }
    &.col-2{
      grid-column: 2 span;
      @include small-tablet{
        grid-column: unset;
      }
    }
  }

  // .advantage__item-img

  &__item-img {
    position: absolute;
    left: 0;
    bottom: 0;
  }

  // .advantage__item-title

  &__item-title {
    line-height: 100%;
    margin: 0;
    margin-bottom: 12px;
  }

  // .advantage__item-text

  &__item-text {
    font-weight: 300;
    color: #ededed;
    margin: 0;
    margin-bottom: 42px;
    max-width: 420px;
    line-height: 120%;
    @include small-tablet{
      margin: 0;
    }
  }

  // .advantage__item-num

  &__item-num {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #fff;
    opacity: 0.2;
    @include small-tablet{
      display: none;
    }
    span{
      display: inline;
      font-size: 8px;
    }
  }
}

.services {
  margin-bottom: 80px;
  @include small-tablet{
    margin-bottom: 50px;
  }
  // .services__title

  &__title {
    text-align: center;
    margin: 0;
    margin-bottom: 25px;
  }

  // .services__subtitle

  &__subtitle {
    text-align: center;
    max-width: 440px;
    margin: 0 auto;
    color: #1c1c1c;
    opacity: 0.4;
    margin-bottom: 20px;
  }

  // .services__avatars

  &__avatars {
    display: flex;
    justify-content: center;
    max-width: 475px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 80px;
    @include mobile{
      margin-bottom: 32px;
    }
    &::after{
      display: block;
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      transform: translateY(-50%);
      height: 1px;
      background: #1c1c1c;
      opacity: 0.1;
    }
  }

  &__avatars-list{
    background-color: white;
    position: relative;
    z-index: 2;
  }

  // .services__avatar

  &__avatar {
    max-width: 50px;
    @include mobile{
      max-width: 40px;
      width: 40px;
      height: 40px;
    }
    &:nth-child(1){
      margin-left: 40px;
    }
    &:nth-child(2){
      transform: translateX(-10px);
    }
    &:nth-child(3){
      transform: translateX(-20px);
    }
    &:nth-child(4){
      transform: translateX(-30px);
    }
    &:nth-child(5){
      transform: translateX(-40px);
    }
  }

  // .services__list

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    margin-bottom: 50px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .services__item

  &__item {
    display: flex;
    gap: 27px;
    @include mobile{
      flex-direction: column;
      display: none;
      &:nth-child(1){
        display: block;
      }
      &:nth-child(2){
        display: block;
      }
      &:nth-child(3){
        display: block;
      }
    }
  }

  // .services__item-img-wrapper

  &__item-img-wrapper {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    width: 100%;
    max-width: 274px;
    @include mobile{
      align-items: flex-start;
      max-width: unset;
      gap: 10px;
      margin-bottom: 20px;
    }
  }

  // .services__item-img

  &__item-img {
    display: block;
    @include mobile{
      width: 120px;
      height: 120px;
      flex: none;
    }
  }

  &__item-brief{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    position: absolute;
    bottom: 15px;
    left: 15px;
    @include mobile{
      position: static;
      flex-direction: row;
      gap: 7px;
      margin-bottom: 7px;
      align-items: stretch;
      flex-wrap: wrap;
    }
  }

  // .services__item-tag

  &__item-tag {
    font-family: var(--font-third);
    font-weight: 400;
    font-size: 12px;
    line-height: 118%;
    color: #fff;
    padding: 11px;
    border-radius: 24px;
    background-color: #1c1c1c;
    @include mobile{
      font-size: 10px;
      padding: 7px;
      border-radius: 15px;
    }
  }

  &__item-time {
    font-family: var(--font-third);
    font-weight: 400;
    font-size: 12px;
    line-height: 118%;
    color: #1c1c1c;
    padding: 11px;
    background-color: white;
    border-radius: 24px;
    @include mobile{
      border: 1px solid #1c1c1c;
      font-size: 10px;
      padding: 7px;
      border-radius: 15px;
    }
  }

  // .services__item-info

  &__item-info {
  }

  &__item-info-main{

  }

  // .services__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      margin-bottom: 5px;
    }
    span {
      font-family: var(--font-family-accent);
      font-weight: 500;
      font-size: 23px;
      line-height: 130%;
      text-align: right;
      color: #fff;
      padding: 10px 14px;
      border-radius: 10px;
      background: #1c1c1c;
    }
  }

  // .services__item-text

  &__item-text {
    font-size: 16px;
    color: #1c1c1c;
    opacity: 0.4;
    margin-bottom: 20px;
    @include mobile{
      line-height: 110%;
      font-size: 12px;
      margin: 0;
    }
  }

  // .services__item-services

  &__item-services {
    display: flex;
    flex-direction: column;
    @include mobile{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 6px;
    }
  }

  // .services__item-service

  &__item-service {
    display: flex;
    align-items: center;
    gap: 24px;
    padding: 12px 0;
    @include mobile{
      padding: 10px 12px;
      border: 1px solid #bbbbbb;
      border-radius: 8px;
    }
    @include hover{
      svg{
        *{
          transition: unset;
        }
        circle{
          fill: var(--accent-color);
        }
        path{
          fill: white;
        }
      }
    }
    svg{
      min-width: 36px;
      min-height: 36px;
      @include mobile{
        display: none;
      }
    }
    &:first-child{
      padding-top: 0;
      @include mobile{
        padding-top: 10px;
      }
    }

    &:not(:last-child){
      border-bottom: 1px solid #e8e8e8;
      @include mobile{
        border: 1px solid #bbbbbb;
      }

    }
  }

  // .services__item-service-text

  &__item-service-text {
    font-weight: 700;
    font-size: 15px;
    line-height: 110%;
    color: #1c1c1c;
    max-width: 110px;
    @include mobile{
      font-size: 12px;
      font-weight: 300;
      max-width: 85px;
    }
  }

  // .services__item-service-price

  &__item-service-price {
    margin-left: auto;
    font-weight: 400;
    font-size: 19px;
    line-height: 110%;
    color: #1c1c1c;
    @include mobile{
      font-size: 16px;
      color: var(--accent-color);
    }
  }

  // .services__item-tool

  &__item-tool {
    font-weight: 400;
    font-size: 12px;
    line-height: 118%;
    color: #fff;
    padding: 11px;
    border-radius: 24px;
    background-color: var(--accent-color);
    @include mobile{
      font-size: 10px;
      padding: 7px;
      border-radius: 15px;
    }
  }

  // .services__more-link

  &__more-link {
    padding: 25px;
    border: 1px solid #1c1c1c;
    border-radius: 15px;
    display: flex;
    justify-content: center;

    font-weight: 700;
    font-size: 17px;
    line-height: 176%;
    text-align: center;
    color: #1c1c1c;
    @include hover{
      background-color: black;
      color: white;
    }
    @include mobile{
      padding: 20px;
    }
  }
}

.stocks {
  margin-bottom: 100px;
  @include small-tablet{
    margin-bottom: 50px;
  }
  // .stocks__content

  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 40px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .stocks__head

  &__head {
  }

  // .stocks__title

  &__title {
    text-transform: uppercase;
    letter-spacing: 0.03em;
    max-width: 470px;
    margin: 0;
    margin-bottom: 40px;
    @include mobile{
      margin-bottom: 15px;
    }
  }

  // .stocks__subtitle

  &__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: #1c1c1c;
    opacity: 0.4;
  }

  // .stocks__stock

  &__stock {
    display: flex;
    flex-direction: column;
    padding: 35px 27px 38px 40px;
    border: 1px solid #e8e8e8;
    @include mobile{
      padding: 20px 15px;

      &:nth-child(3){
        order: 2;
      }
      &:nth-child(4){
        order: 4;
      }
    }
  }

  // .stocks__stock-invite

  &__stock-invite {
    padding: 0;
    border: none;
    @include mobile{
      order: 3;
      border: 1px solid #e8e8e8;
      padding: 20px 15px;
    }
  }

  // .stocks__stock-title

  &__stock-title {
    margin: 0;
    margin-bottom: 20px;
    @include mobile{
      margin-bottom: 0;
      align-self: flex-start;
      br{
        display: none;
      }
    }
    span{
      margin-top: 12px;
      color: var(--accent-color);
      text-transform: uppercase;
    }
  }

  // .stocks__stock-links

  &__stock-links {
    display: grid;
    grid-template-columns: auto auto;
    gap: 13px;
    margin-top: auto;
    @include mobile{
      grid-template-columns: 1fr;
      svg{
        display: none;
      }
    }
  }

  // .stocks__stock-link-use

  &__stock-link-use {
    display: flex;
    align-items: center;
    gap: 40px;
    padding: 22px 25px;
    font-weight: 700;
    line-height: 176%;
    color: #fff;
    background-color: var(--accent-color);
    &--w100{
      width: 100%;
      justify-content: space-between;
    }
    @include hover{
      color: black;
      svg{
        path{
          fill: black;
        }

      }
    }
    @include mobile{
      justify-content: center;
    }
  }

  // .stocks__stock-link-conditions

  &__stock-link-conditions {
    color: #1c1c1c;
    padding: 22px 25px;
    border: 1px solid #1c1c1c;
    display: flex;
    align-items: center;
    font-weight: 700;
    line-height: 176%;
    @include hover{
      background-color: var(--accent-color);
      color: white;
      border: 1px solid white;
    }
    @include mobile{
     justify-content: center;
    }
  }

  // .stocks__stock-head

  &__stock-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      flex-direction: column;
      gap: 10px;
    }
  }

  // .stocks__stock-img

  &__stock-img {
    max-width: 250px;
    @include mobile{
      margin-bottom: -25px;
    }
  }

  // .stocks__stock--accent

  &__stock--accent {
    border: none;
    background-color: var(--accent-color);
    color: white;
    @include mobile{
      background-color: white;
      border: 1px solid #e8e8e8;
    }
    .stocks__stock-img{
      max-height: 180px;
      margin-right: 50px;
      @include mobile{
        margin-right: -70px;
        margin-top: -40px;
        margin-bottom: -20px;
        max-width: 250px;
      }
    }
