  // .about__item-appointments-link

  &__item-appointments-link {
    font-weight: 700;
    font-size: 26px;
    line-height: 140%;
    color: rgba(255, 255, 255, 0.8);
  }
}

.contacts {
  padding: 60px 0;
  background-color: #f7f7f7;
  @include mobile{
    padding: 30px 0;
  }
  &--dark{
    background-color: #1c1c1c;
  }
  // .contacts__content

  &__content {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 410px);
    gap: 20px;
    margin-bottom: 45px;
    @include tablet{
      grid-template-columns: 1fr;
    }
    @include mobile{
      margin-bottom: 20px;
    }
  }

  // .contacts__info

  &__info {
    background-color: white;
    padding: 50px;
    border-radius: 30px;
    @include mobile{
      padding: 25px 20px;
      border-radius: 30px;
    }
  }

  // .contacts__info-head

  &__info-head {
    display: flex;
    align-items: flex-start;
    gap: 180px;
    margin-bottom: 15px;
    @include mobile{
      gap: 20px;
      justify-content: space-between;
    }
  }

  // .contacts__info-title

  &__info-title {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 700;
    font-size: 33px;
    line-height: 100%;
    letter-spacing: -0.04em;
    color: #1c1c1c;
    text-transform: none;
    margin: 0;
    @include mobile{
      font-size: 25px;
    }
  }

  // .contacts__info-rating

  &__info-rating {
    max-height: 45px;
    @include mobile{
      height: 35px;
    }
  }

  // .contacts__info-list

  &__info-list {
    display: grid;
    grid-template-columns: minmax(200px, 326px) 1fr;
    column-gap: 50px;
    row-gap: 15px;
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .contacts__info-item

  &__info-item {
    display: flex;
    flex-direction: column;
    gap: 5px;

    &:has(.contacts__info-social){
      order: 4;
    }
  }

  // .contacts__info-name

  &__info-name {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    opacity: 0.4;
  }

  // .contacts__info-text

  &__info-text {
    font-weight: 700;
    font-size: 25px;
    line-height: 140%;
    letter-spacing: -0.04em;
    color: rgba(16, 18, 16, 0.8);
    @include mobile{
      font-size: 18px;
    }
  }

  &__info-mail{
    text-decoration: underline;
  }

  // .contacts__info-social

  &__info-social {
    display: flex;
    gap: 10px;

  }

  // .contacts__info-social-link

  &__info-social-link {
    width: 50px;
    height: 50px;
    flex: none;
    border-radius: 100%;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    @include hover{
      background-color: black;
      svg{
        path{
          fill: white;
        }
      }
    }
    @include mobile{
      width: 35px;
      height: 35px;
      svg{
        width: 12px;
      }
    }
  }

  // .contacts__promocode

  &__promocode {
    padding: 50px;
    border-radius: 30px;
    background-color: white;
    @include mobile{
      padding: 25px 20px;
      border-radius: 20px;
    }
  }

  &__promocode-title{
    margin-bottom: 15px;
  }

  // .contacts__promocode-text

  &__promocode-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    margin: 0;
    margin-bottom: 20px;
    span{
      display: inline;
      font-weight: 700;
    }
  }

  // .contacts__promocode-link

  &__promocode-link {
    width: 100%;
    text-align: center;
    padding: 27px;
    margin-bottom: 20px;
  }

  // .contacts__promocode-additionally

  &__promocode-additionally {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    opacity: 0.4;
  }

  // .contacts__map-wrapper

  &__map-wrapper {

  }

  // .contacts__map

  &__map {
    width: 100%;
    height: 100%;
    max-height: 480px;
    border: 30px;
    border-radius: 30px;
    height: 480px;
    overflow: hidden;
    @include mobile{
      height: 200px;
      border-radius: 20px;
    }
  }
}
