.hero-training{

  &__advantages{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    max-width: 550px;
    @include small-tablet{
      width: 100%;
      max-width: unset;
    }
    @include mobile{
      grid-template-columns: 1fr;
      gap: 20px;
      margin-bottom: 40px;
    }
  }

  &__advantage{
    background: linear-gradient(90deg, #2e2e2e 0%, #181818 100%);
    padding: 50px 5px 43px 17px;
    position: relative;
    &:nth-child(1){
      border-radius: 17px 0 0 17px;
      border: 1px solid rgba(255, 255, 255, 0.15);
    }
    &:nth-child(2){
      border-top:  1px solid rgba(255, 255, 255, 0.15);
      border-bottom:  1px solid rgba(255, 255, 255, 0.15);
    }
    &:nth-child(3){
      border-radius: 0 17px 17px 0;
      border: 1px solid rgba(255, 255, 255, 0.15);
    }
    &:nth-child(n){
      @include small-tablet{
        border-radius: 0;
        border: 0;
      }
    }
    @include mobile{
      background: unset;
      display: flex;
      align-items: center;
      gap: 15px;
      padding: 0;
    }
  }

  &__advantage-icon{
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--accent-color);
    border-radius: 4px;
    position: absolute;
    top: -20px;
    left: 17px;
    @include mobile{
      position: static;
      flex: none;
    }
  }

  &__advantage-text{
    color: white;
    display: inline;
    font-size: 15px;
    @include mobile{
      br{
        display: none;
      }
    }
    span{
      display: inline;
      font-weight: 700;
    }
  }

  .hero__info-content{
    @include small-tablet{
      width: 100%;
    }
  }
  .hero__promocode-link{
    margin-bottom: 60px;
  }
}

.individual-training {
  margin-bottom: 100px;
  @include small-tablet{
    margin-bottom: 50px;
  }
  // .individual-training__head

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-bottom: 70px;
    @include small-tablet{
      margin-bottom: 35px;
    }
  }

  // .individual-training__title

  &__title {
    margin: 0;

  }

  // .individual-training__img

  &__img {
    max-width: 150px;
    @include small-tablet{
      display: none;
    }
  }

  // .individual-training__content

  &__content {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 485px);
    gap: 15px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .individual-training__info

  &__info {
    padding: 50px;
    background-color: #1c1c1c;
    border-radius: 20px;
    @include mobile{
      padding: 25px 20px;
    }
  }

  // .individual-training__info-title

  &__info-title {
    margin: 0;
    color: white;
    margin-bottom: 20px;
    @include mobile{
      br{
        display: none;
      }
    }
  }



  // .individual-training__list

  &__list {
    columns: 2;
    padding-left: 20px;
    margin: 0;
    @include mobile{
      columns: 1;
    }
  }

  // .individual-training__item

  &__item {
    color: white;
    max-width: 215px;
    margin-bottom: 10px;
  }

  // .individual-training__info-2

  &__info-2 {
    position: relative;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    overflow: hidden;
    padding: 50px 30px 50px 40px;
    @include mobile{
      padding: 25px 20px;
      padding-bottom: 300px;
    }
    .individual-training__info-title{
      color: black;
      position: relative;
      z-index: 1;
    }
    .individual-training__list{
      columns: 1;
      position: relative;
      z-index: 1;
    }
    .individual-training__item{
      color: black;
    }
  }

  // .individual-training__info-img

  &__info-img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 265px;
    @include mobile{
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      bottom: 0;
      top: unset;
      height: 280px;
      object-fit: contain;
    }
  }
}
