.footer {
  &--dark{
    background-color: #1c1c1c;
  }
  // .footer__content

  &__content {
    padding: 30px 50px;
    background-color: white;
    border-radius: 30px;
    display: grid;
    grid-template-columns: minmax(100px, 186px) 1fr minmax(100px, 230px);
    gap: 30px;
    @include tablet{
      grid-template-columns: minmax(100px, 186px) 1fr;
    }
    @include small-tablet{
      grid-template-columns: 1fr
    }
    @include mobile{
      padding: 25px 15px;
    }
  }

  // .footer__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    @include small-tablet{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 5px;
    }
  }

  // .footer__logo

  &__logo {
    max-width: 59px;
    @include mobile{
      max-width: 45px;
    }
  }

  // .footer__info-text

  &__info-text {
    font-weight: 400;
    font-size: 13px;
    line-height: 130%;
    color: #242424;
    @include small-tablet{
      grid-column: 2/3;
      grid-row: 1/3;
      text-align: right;
    }
  }

  // .footer__info-reserved

  &__info-reserved {
    font-weight: 400;
    font-size: 13px;
    line-height: 130%;
    color: #242424;
    opacity: 0.4;
  }

  // .footer__nav

  &__nav {

  }

  // .footer__nav-list

  &__nav-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
  }

  // .footer__nav-block

  &__nav-block {
  }

  // .footer__nav-name

  &__nav-name {
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    margin-bottom: 13px;
    @include mobile{
      font-size: 13px;
      opacity: 0.3;
    }
  }

  // .footer__nav-item-list

  &__nav-item-list {
  }

  // .footer__nav-item

  &__nav-item {
    display: grid;
    row-gap: 20px;
    @include mobile{
      row-gap: 15px;
    }
    &.col-2{
      grid-template-columns: repeat(2, 1fr);
      column-gap: 25px;
      @include small-tablet{
        grid-template-columns: 1fr;
      }
    }
  }

  // .footer__nav-link

  &__nav-link {
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    text-wrap: nowrap;
    @include hover{
     color: var(--accent-color);
    }
    @include mobile{
      font-size: 13px;
    }
  }

  // .footer__contacts

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 25px;
    @include tablet{
      display: none;
    }
  }

  // .footer__contacts-item

  &__contacts-item {
    display: flex;
    flex-direction: column;
    gap: 7px;
  }

  // .footer__contacts-name

  &__contacts-name {
    font-weight: 400;
    font-size: 13px;
    color: rgba(36, 39, 41, 0.6);
    opacity: 0.4;
  }

  // .footer__contacts-text

  &__contacts-text {
    font-weight: 700;
    font-size: 17px;
    color: #242729;
  }

  // .footer__contacts-link

  &__contacts-link {
    @include hover{
      color: var(--accent-color);
    }
  }

  &__contacts-mail {
    text-decoration: underline;
  }
}
