.certificates-info {
  margin-bottom: 100px;
  @include small-tablet{
    margin-bottom: 50px;
  }
  // .certificates-info__content

  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .certificates-info__item

  &__item {
    border-radius: 20px;
    @include hover{
      svg {
        path {
          fill: white;
        }
        circle{
          fill: var(--accent-color)
        }
      }
    }
    &:nth-child(1){
      grid-row: 1/3 span;
      @include tablet{
        grid-row: unset;
      }
    }
    &:nth-child(2){
      grid-row: 1/2 span;
      @include tablet{
        grid-row: unset;
      }
    }
    &:nth-child(3){

    }
    &:nth-child(4){

    }
    &:nth-child(5){

    }
  }

  // .certificates-info__item-info

  &__item-info {
    display: grid;
    grid-template-columns: 1fr minmax(100px, 270px);
    gap: 20px;
    background-color: #1c1c1c;
    @include mobile{
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  // .certificates-info__item-prices

  &__item-prices {
    padding: 40px;
    @include mobile{
      order: 2;
      padding: 25px 20px;
    }
  }

  // .certificates-info__item-title

  &__item-title {
    font-weight: 700;
    @include fluid-text(28, 18);
    line-height: 100%;
    color: #fff;
    margin: 0;
    margin-bottom: 9px;
  }

  // .certificates-info__item-text

  &__item-text {
    font-weight: 300;
    font-size: 15px;
    line-height: 110%;
    color: #fff;
    margin-bottom: 10px;
  }

  // .certificates-info__item-prices-list

  &__item-prices-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  // .certificates-info__item-prices-item

  &__item-prices-item {
    font-family: var(--font-family-accent);
    font-weight: 500;
    @include fluid-text(35, 30);
    line-height: 110%;
    text-align: right;
    color: #fff;
    padding: 8px 14px;
    border: 1px solid #606060;
    border-radius: 10px;
    text-align: center;
    &.col-2{
      grid-column: 2 span;
    }
  }

  // .certificates-info__item-prices-item--white

  &__item-prices-item--white {
    background-color: white;
    color: #1c1c1c;
  }

  // .certificates-info__item-info-img

  &__item-info-img {
    height: 100%;
    border-radius: 20px;
    @include mobile{
      width: 100%;
    }
  }

  // .certificates-info__item-content

  &__item-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    background-color: #f8f8f8;
    border-radius: 20px;
    @include mobile{
      grid-template-columns: 1fr;
      gap: 0;
      position: relative;
      .certificates-info__item-title{
        margin: 0;
      }
    }
    .certificates-info__item-title{
      color: black;
    }
    .certificates-info__item-title{
      grid-column: 2 span;
      @include mobile{
        order: 1;
        grid-column: unset;
      }
    }
  }

  // .certificates-info__item-content-info

  &__item-content-info {
    padding: 40px 0 40px 40px;
    display: grid;
    grid-template-columns: auto auto;
    justify-items: start;
    align-items: start;
    gap: 16px;
    @include mobile{
      order: 2;
      padding: 25px 20px;
      border-radius: 12px;
      background-color: #f8f8f8;
      margin-top: -55px;
      align-items: center;
    }
    svg{
      justify-self: end;
      @include mobile{
        order: 2;
      }
    }
  }

  // .certificates-info__item-name

  &__item-name {
    font-family: var(--font-third);
    font-weight: 400;
    font-size: 12px;
    line-height: 118%;
    color: #fff;
    padding: 11px;
    background-color: #1c1c1c;
    border-radius: 24px;
    @include mobile{
      display: none;
    }
  }

  // .certificates-info__item-content-img

  &__item-content-img {
    height: 100%;
    border-radius: 20px;
    @include mobile{
      order: 1;
    }
  }
}
