.faq {
  background-color: #1c1c1c;
  padding-bottom: 20px;
  // .faq__title

  &__title {
    margin-bottom: 50px;
    line-height: 100%;
  }

  // .faq__content

  &__content {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 410px);
    gap: clamp(2.5rem, 1.324rem + 5.88vw, 5.625rem);
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .faq__info

  &__info {
    display: flex;
    flex-direction: column;
    gap: 18px;
    @include mobile{
      gap: 25px;
    }
  }

  // .faq__info-item

  &__info-item {
    padding: 45px 40px;
    border-radius: 30px;
    background-color: white;
    @include mobile{
      border-radius: 20px;
      padding: 25px 20px;
    }
  }

  // .faq__info-title

  &__info-title {
    font-family: var(--font-family-accent);
    font-style: italic;
    font-weight: 900;
    @include fluid-text(45, 30);
    line-height: 100%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #1c1c1c;
    max-width: 280px;
    margin-bottom: 20px;
  }

  // .faq__info-text

  &__info-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
    margin-bottom: 8px;
    max-width: 280px;
    opacity: 0.4;
  }

  // .faq__info-link

  &__info-link {
    font-weight: 700;
    font-size: 25px;
    line-height: 140%;
    letter-spacing: -0.04em;
    color: rgba(16, 18, 16, 0.8);
  }

  // .faq__form

  &__form {

  }

  // .faq__form-title

  &__form-title {
    font-family: var(--font-family-accent);
    font-style: italic;
    font-weight: 900;
    @include fluid-text(35, 30);
    line-height: 100%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #1c1c1c;
    margin-bottom: 25px;
    @include mobile{
      margin-bottom: 20px;
      br{
        display: none;
      }
    }
  }

  // .faq__form-wrapper

  &__form-wrapper {
    padding-left: 25px;
    border: 1px solid #ededed;
    border-radius: 20px;
    background-color: rgba(247, 249, 252, 0.9);
    display: flex;
    justify-content: space-between;
    gap: 10px;
    @include mobile{
      flex-direction: column;
      border: 0;
      background-color: unset;
      padding: 0;
      gap: 11px;
    }
  }

  // .faq__form-input

  &__form-input {
    @include mobile{
      border: 1px solid #ededed;
      border-radius: 20px;
      background-color: rgba(247, 249, 252, 0.9);
      padding: 19px;
    }
    &::placeholder{
      font-weight: 400;
      font-size: 15px;
      color: #242729;
    }
  }

  // .faq__form-submit

  &__form-submit {
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background-color: #c2ae8c;
    @include mobile{
      width: 100%;
      height: unset;
      padding: 20px;
    }
  }

  &__form-submit-text{
    font-weight: 700;
    font-size: 14px;
    color: #fff;
  }
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 15px;
  // .accordion__item

  &__item {
    border-radius: 16px;
    backdrop-filter: blur(30.125211715698242px);
    background: rgba(255, 255, 255, 0.03);
    padding: 15px 25px;
    border: 1px solid #909090;
    &--active{
      border: 1px solid #c2ae8c;
      .accordion__head-ques{
        color: #c2ae8c;
      }
      .accordion__head{
        svg{
          transform: rotate(180deg);
          path{
            fill: #C2AE8C;
          }
        }
      }
    }
  }

  // .accordion__head

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 20px;
    svg{
      transition: .2s ease-in;
    }
  }

  // .accordion__head-ques

  &__head-ques {
    font-weight: 700;
    @include fluid-text(19, 16);
    line-height: 140%;
    color: #f5f5f8;
    transition: .2s ease-in;
  }

  // .accordion__head-indicator

  &__head-indicator {
  }

  // .accordion__content

  &__content {
    height: 0;
    overflow: hidden;
    transition: .2s ease;

    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    transition: .2s ease-in;
  }

  // .accordion__content-body

  &__content-body {

  }

  // .accordion__text

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: #f5f5f8;
    display: block;
    margin: 0;
    margin-top: 20px;
    span{
      display: inline;
      text-decoration: underline;
    }
  }
}
