.hero-barbers{
  &__text-num{
    margin-bottom: 23px;
  }

  &__text{
    margin: 0;
  }

  &__img{
    @include mobile{
      object-position: right;
    }
  }
}

.barbers {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .barbers__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: clamp(0.938rem, 0.349rem + 2.94vw, 2.5rem);
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .barbers__item

  &__item {
    border-radius: 12px;
    overflow: hidden;
    background-color: #f8f8f8;
  }

  // .barbers__item-head

  &__item-head {
    height: clamp(20rem, 18.353rem + 8.24vw, 24.375rem);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: flex-end;
    padding: 20px;
    border-radius: 20px;
    @include mobile{
      background-position: top;
    }
  }

  // .barbers__item-tags

  &__item-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  // .barbers__item-tag

  &__item-tag {
    font-family: var(--font-third);
    padding: 12px 18px;
    border-radius: 30px;
    font-weight: 700;
    font-size: 12px;
    line-height: 118%;
    color: #1c1c1c;
    @include mobile{
      padding: 10px 15px;
    }
  }

  // .barbers__item-tag--white

  &__item-tag--white {
    font-weight: 700;
    background-color: white;
  }

  // .barbers__item-tag--black

  &__item-tag--black {
    color: white;
    background-color: #1c1c1c;
  }

  // .barbers__item-text

  &__item-text {
    margin: 0;
    padding: 20px;

    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: rgba(16, 18, 16, 0.8);
  }
}
