.works {
  background-color: #1c1c1c;
  padding-bottom: 50px;

  // .works__title

  &__title {
    margin: 0;
    margin-bottom: 70px;
    line-height: 100%;
    @include small-tablet{
      margin-bottom: 25px;
    }
  }

  // .works__content

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(6, auto);
    gap: 15px;
    @include mobile{
      grid-template-columns: 1fr;
      grid-template-rows: unset;
      gap: 13px;
    }
  }

  // .works__img

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    &:nth-child(10n + 1){
      grid-column: 1/5;
      grid-row: 1/3;
    }
    &:nth-child(10n + 2){
      grid-column: 5/9;
    }
    &:nth-child(10n + 3){
      grid-column: 5/9;
      grid-row: 2/3;
    }
    &:nth-child(10n + 4){
      grid-column: 9/13;
      grid-row: 1/3;
    }
    &:nth-child(10n + 5){
      grid-column: 1/7;
      grid-row: 3/5;
    }
    &:nth-child(10n + 6){
      grid-column: 7/13;
      grid-row: 3/5;
    }
    &:nth-child(10n + 7){
      grid-column: 1/8;
      grid-row: 5/7;
    }
    &:nth-child(10n + 8){
      grid-column: 8/13;
    }
    &:nth-child(10n + 9){
      grid-column: 8/11;
    }
    &:nth-child(10n + 10){
      grid-column: 11/13;
    }
    &:nth-child(n){
      @include mobile{
        grid-column: unset;
        grid-row: unset;
      }
    }
  }
}
